import React, { Component } from 'react';
//@ts-ignore
import Bootstrap, { Button } from 'bootstrap-4-react';

type TGameDescriptionProps ={

}

export default class App extends Component<TGameDescriptionProps> {

    componentWillMount() {
        window.setTimeout(() => Bootstrap.popover('#popoverExample'), 2000);
    }

    render() {
        const popover = {
            title: `It's so easy !` ,
            content: 'you just need to click on the cards as soon as you choose 2 identical cards - ' +
                'they will be searched, you will win when you guess all the cards'
        }

        return (
            <Button  lg warning  popover={popover} id="popoverExample">
               how to play?
            </Button>
        )
    }
}