import React from 'react';
//@ts-ignore
import  {Carousel} from 'bootstrap-4-react';
type TSliderCationProps = {
    captionTitle? : string
    caption? : string
    textColor ? : "primary" | "secondary" | "success" | "danger" | "warning" | "info" |
        "muted" | "light" | "dark"
}

const SliderCaption : React.FC<TSliderCationProps> = (props) => {
    return(
        <Carousel.Caption shadow="none" display="none md-block"  text={props.textColor}>
            <h3>
                {!props.captionTitle ? "Stepanenko Vladislav" : props.captionTitle }
            </h3>
            <p>
                {!props.caption ? " welcome, here you can find out some information about me as a developer and my interests in life"
                : props.caption
                }
            </p>
        </Carousel.Caption>
    )
}

export default SliderCaption