import React, {Component} from 'react';
//@ts-ignore
import Bootstrap, {Carousel, BImg, Progress} from 'bootstrap-4-react';
import Forest1 from "../../utils/imgs/bg1.jpg"
import Forest2 from "../../utils/imgs/bg2.jpg"
import Forest3 from "../../utils/imgs/bg3.jpg"
import Forest4 from "../../utils/imgs/bg4.jpg"
import Forest5 from "../../utils/imgs/bg5.jpg"
import SliderCaption from "./SliderCaption";

type TSliderProps = {
    SetHPIsLoading: (bool: boolean) => void
    HPIsLoading: boolean
}

export default class Slider extends Component<TSliderProps> {

    componentWillMount() {
        window.setTimeout(() => Bootstrap.carousel('#carouselExampleFade'), 2000);
    }

    componentDidMount() {
        this.props.SetHPIsLoading(false)
    }

    componentWillUnmount() {
        this.props.SetHPIsLoading(true)
    }

    render() {
        const SliderStyle = {
            height: '93.38vh',
            width: '100%'
        };
        return (
            <>
                {
                    !this.props.HPIsLoading ?
                        <Carousel w="100" id="carouselExampleFade" fade>
                            <Carousel.Inner>
                                <Carousel.Item active>
                                    <BImg style={SliderStyle} display="block" src={Forest2}/>
                                    <SliderCaption textColor="light"/>
                                </Carousel.Item>
                                <Carousel.Item>
                                    <BImg style={SliderStyle} display="block" src={Forest1}/>
                                    <SliderCaption textColor="light"/>
                                </Carousel.Item>
                                <Carousel.Item>
                                    <BImg style={SliderStyle} display="block" src={Forest3}/>
                                    <SliderCaption/>
                                </Carousel.Item>
                                <Carousel.Item>
                                    <BImg style={SliderStyle} display="block" src={Forest4}/>
                                    <SliderCaption/>
                                </Carousel.Item>
                                <Carousel.Item>
                                    <BImg style={SliderStyle} display="block" src={Forest5}/>
                                    <SliderCaption/>
                                </Carousel.Item>

                            </Carousel.Inner>
                            <Carousel.Prev href="#carouselExampleFade">
                                <Carousel.Prev.Icon/>
                            </Carousel.Prev>
                            <Carousel.Next href="#carouselExampleFade">
                                <Carousel.Next.Icon/>
                            </Carousel.Next>
                        </Carousel>

                        :
                        <Progress mt="4" mb="4">
                            <Progress.Bar striped animated min="0" max="100" now="50"/>
                        </Progress>
                }
            </>
        )
    }
}