
const SET_HOMEPAGE_IS_LOADING = 'TILES/SET_HOMEPAGE_IS_LOADING'


let DefaultState = {
    HPIsLoading : true
}

type  DefaultTilesState = typeof DefaultState


type TTilesReducerActions = ReturnType<typeof SetHPIsLoading>

const LoadingReducer = (state = DefaultState, action : TTilesReducerActions): DefaultTilesState => {

    switch (action.type) {
        case SET_HOMEPAGE_IS_LOADING : {

            return {
                ...state,
                HPIsLoading : action.HPIsLoading
            }
        }
        default :
            return state
    }
}

export const SetHPIsLoading = (HPIsLoading : boolean)  =>{
    return   {type: SET_HOMEPAGE_IS_LOADING, HPIsLoading} as const
}

export default LoadingReducer