import React, {useState} from "react"
import Slider from "../../Slider/Slider";
import {SetHPIsLoading} from "../../../data/LoadingReducer";
import {GlobalState} from "../../../data/redux-store";
import {connect} from "react-redux"

type T_MSTP_HomePageContainer = {
    HPIsLoading: boolean
}

type T_MDTP_HomePageContainer = {
    SetHPIsLoading: typeof SetHPIsLoading
}

type THomePageContainerOwnProps = {}

export type THomePageContainerProps = T_MSTP_HomePageContainer & T_MDTP_HomePageContainer & THomePageContainerOwnProps

class HomePageContainer extends React.Component<THomePageContainerProps> {
    render() {

        return (
            < Slider HPIsLoading={this.props.HPIsLoading}
                     SetHPIsLoading={this.props.SetHPIsLoading}/>
        )
    }
}

let MapStateToProps = (state: GlobalState): T_MSTP_HomePageContainer => ({
    HPIsLoading: state.LoadingData.HPIsLoading
})

export default connect<T_MSTP_HomePageContainer, T_MDTP_HomePageContainer, THomePageContainerOwnProps, GlobalState>(MapStateToProps,
    {SetHPIsLoading})(HomePageContainer);
